export default {
	data() {
		return {
			list: [],
			rds: []
		}
	},
	onLoad: function() {
		var _this = this
		this.$https({
			url: '/api/user/my-address',
			data: {},
			dengl: false,
			success: function(res) {
				res.data.data.map(function(n, index) {
					n.index = index
				})
				_this.list = res.data.data
			}
		})
	},
	methods: {
		tianjia: function() {
			var _this = this
			wx.chooseAddress({
				success: function(res) {
					//添加收货地址
					_this.$https({
						url: '/api/user/address-add-edit',
						data: JSON.stringify({
							address: res.detailInfo,
							cityInfo: res.provinceName + res.cityName + res.countyName,
							phone: res.telNumber,
							username: res.userName
						}),
						haeder: true,
						dengl: false,
						method: 'post',
						success: function(res) {
							//刷新地址列表
							_this.$https({
								url: '/api/user/my-address',
								data: {},
								dengl: false,
								success: function(res) {
									res.data.data.map(function(n, index) {
										n.index = index
									})
									_this.list = res.data.data
								}
							})
						}
					})
				}
			})
		},
		checkboxChange: function(e) {
			this.rds = e.detail.value
		},
		bianji: function(index) {
			uni.navigateTo({
				url: 'address?address=' + JSON.stringify(this.list[index])
			})
		}
	}
}
